@charset "UTF-8";
@font-face {
  font-family: "Main";
  src: url("~@/src/assets/Futura LT Heavy.otf"); }

html,
body {
  font-size: calc(100vw/375*100);
  height: 100%; }

@media screen and (min-width: 600px) {
  html {
    font-size: 160px; } }

@media screen and (min-width: 750px) {
  html {
    font-size: 195px; } }

#root {
  min-height: 100%;
  position: relative;
  overflow: hidden;
  padding-bottom: 0.8rem;
  box-sizing: border-box; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0; }
  to {
    opacity: 1; } }

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
