video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

/* Don't kill focus outline for keyboard users: http://24ways.org/2009/dont-lose-your-focus */
a:hover,
a:active {
  outline: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/*这里定义整个应用加载时的动画，进场前这里设置了缩放为0，
进场过程中缩放变为1，也就是100%，持续时间为500ms，
时间曲是ease-out，一种先快后慢的变化曲线*/
.appAppear-appear {
  opacity: 0;
  transform: scale(0); }

.appAppear-appear.appAppear-appear-active {
  opacity: 1;
  transform: scale(1);
  transition: 500ms ease-out; }

/*设置进场前透明度为0，放缩为0*/
.fade-enter {
  opacity: 0; }

/*设置进场过程中 透明度为1，放缩为1，放缩的位置为左上角，持续时间为500ms，时间曲是ease-out */
.fade-enter-active {
  opacity: 1;
  transition: all 500ms ease-out; }

/*设置退场前透明度为1，放缩为1*/
.fade-exit {
  opacity: 1; }

/*设置退场过程中 透明度为0，放缩为0，放缩的位置默认是中间，所以不设置也可以，
  持续时间为500ms，时间曲是ease-out */
.fade-exit-active {
  position: absolute;
  z-index: -1;
  opacity: 0;
  transition: all 0ms ease-out; }

