/*  文章版式 */
.article {
  width: 3.75rem;
  min-height: 100vw;
  margin: 0.2rem auto 0.5rem auto;
  font-size: 16px;
  font-family: PingFang SC, Microsoft Yehei, Hiragino Sans GB;
  color: #555;
